.information-subscription {
  display: flex;
  justify-content: space-between;
  margin: 0px 20px;
}

.information-subscription-right {
  p {
    margin: 0;
    color: $gold;
  }
}

.information-subscription-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.information-subscription-expire {
  color: $gold;
  background-color: $black_low;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid $gold;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 120px;
  height: 60px;
  justify-content: space-between;
  p {
    margin: 0;
  }
  span {
    color: $white;
  }
}

.btn-report-payment {
  border: none;
  color: $gold;
  border-radius: 5px;
  width: 132px;
}

.box-subscription-plan {
  background-color: $black_low;
  border: 1px solid $gold;
  color: $gold;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  width: 50%;
position: relative;
}

.subscription-plans {
  h1 {
    text-align: center;
  }
}

.month-plan {
  span:nth-child(2) {
    font-size: 2em;
  }
  span:nth-child(1) {
    font-size: 5em;
  }
}

.price-plan {
  font-weight: bold;
  margin: 0;
  margin-bottom: 30px;
}

.container-subscription-plans {
  display: flex;
  justify-content: space-between;
}

.btn-plan-subscription {
  position: absolute;
  bottom: -14px;
  border: none;
  border-radius: 5px;
  color: $gold;
  font-size: 1.2em;
  width: 70%;
}