.contact-us-main {
position: relative;
height: 100vh;
width: 100%;
}

.container-contact-us-main {
    width: 100%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.background-contact-us {
    position: absolute;
    bottom: 0;
    z-index: -1;
    img {
        width: 100%;
    }
}

.box-contact-us-main {
    background-color: $gold;
    border-radius: 10px;
    margin-right: 20px;
    margin-left: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    div {
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        span {
            margin-right: 10px;
            margin-left: 10px;
        }
        p {
            margin-right: 10px;
            color: $black;
        }
    }
}

.icon-contact-us-web {
  mask: url("../../asset/image/internet.svg");
  display: inline-block;
  width: 30px;
  height: 30px;
  mask-size: cover;
  background: $black;
}

