.title-login {
  text-align: center;
  font-size: 1em;
}

.main-login {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.title-little-login {
  opacity: 0.75;
}

.box-form-login {
  display: flex;
  flex-direction: column;
  width: 80%;
}

.input-login {
  margin-top: 10px;
  margin-bottom: 10px;
  // width: 80%;
}

.input-login-select {
  margin-top: 10px;
  margin-bottom: 10px;
  width: 40%;
  text-align: right;
}
.input-login-select-100 {
  margin-top: 10px;
  margin-bottom: 10px;
  width: 97%;
}

.box-input-login-select {
  display: flex;
  justify-content: space-between;
}

.title-little-login-to-register {
  color:$white;
  font-weight: 100;
  opacity: 1;
}

.icon-back {
  color: $gold;
  width: 100%;
  text-align: end;
  font-size: 1.5em;
}

.box-icon-back {
  width: 80%;
}

.box-logo-login {
  width: 30%;
  img {
    width: 100%;
  }
}

.box-icon-login {
  width: 80%;
  display: flex;
  justify-content: flex-end;
  margin-top:20px;
  i {
    color: $gold;
    font-size: 1.2em;
  }
}

.box-title-little-login {
  width: fit-content;
}

@media only screen and (min-width: 768px) {
  .title-login {
    font-size: 2em;
  }
  .title-little-login {
    font-size: 1em;
  }
  .icon-back {
    width: 100%;
  }
  .box-icon-back {
    width: 100%;
  }
  .box-icon-login {
    i {
      font-size: 2em;
    }
  }
}
