.setting-main {
     width: 100%;
}

.setting-option {
    margin: 0px 10px;
}

.setting-option-button {
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $gold;
    text-align: right;
    color: $gold;
    font-weight: bold;
    padding: 10px;
    display: flex;
    justify-content: space-between;
}

.setting-option-button-delete {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $gold;
    text-align: right;
    color: $gold;
    font-weight: bold;
    padding: 10px;
    i {
        font-size: 1.5em;
    }
}


.icon-edit-and-view {
    mask: url("../../../asset/image/editAndView.svg");
    display: inline-block;
    width: 25px;
    height: 25px;
    mask-size: cover;
    background: $gold;
  }