.MenuBottom {
  display: flex;
  width: 100%;
  background-color: $gold;
  border-radius: 20px 20px 0px 0px;
  // position: fixed;
  // left: 0;
  // bottom: 0;
}

.box-MenuBottom {
    display: flex;
    justify-content: space-around;
    width: 100%;
    width: inherit;
    flex-direction: row-reverse;
}

.option-MenuBottom {
  padding: 5px 15px 2px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  background-color: transparent;
  border: none;
  color: $black;
}

#option-MenuBottom-setting {
  mask: url("../../asset/image/setting.svg");
  display: inline-block;
  width: 30px;
  height: 30px;
  mask-size: cover;
  background: $black;
}

