.runLoading-main {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  flex-direction: column;
}

.box-logo-runLoading {
  // width: 0;
  position: absolute;
  transform: scale(0);
  background-color: $black;
  border-radius: 50%;
  padding: 0px;
  animation-name: logo-runLoading;
  animation-duration: 2s; /* or: Xms */
  animation-direction: alternate; /* or: normal */
  animation-timing-function: ease-in-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  animation-fill-mode: forwards;
  animation-delay: 1s; /* or: Xms */
  // margin-top: -250px;
  opacity: 0;
  // transform: scale(30%);
  transform-origin: center;
}

@keyframes logo-runLoading {
  0% {
    //   width: 0;
    transform: scale(0);
  }
  50% {
    // width: 30%;
    transform: scale(30%);
    padding: 20px;
    opacity: 1;
    margin-top: 0px;
  }
  90% {
    // width: 30%;
    transform: scale(30%);
    padding: 20px;
    opacity: 1;
    margin-top: 0px;
  }
  100% {
    // width: 30%;
    transform: scale(30%);
    padding: 20px;
    opacity: 1;
    margin-top: 0px;
    margin-top: -45vh;
  }
}

.logo-runLoading {
  width: 100%;
}

.text-runLoading {
  color: $gold;
  font-size: 1.2em;
}

.btn-shine {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 12px 48px;
  color: #fff;
  background: linear-gradient(to right, $gold 0, #fff 10%, $gold 20%);
  background-position: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation-duration: 6s;
  animation: shine 2.5s infinite linear , fade-in 2.5s;
  animation-fill-mode: forwards;
  -webkit-text-size-adjust: none;
  font-weight: 600;
  font-size: 16px;
  text-decoration: none;
  white-space: nowrap;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes shine {
  0% {
    background-position: 255px;
  }
  60% {
    background-position: 0;
  }
  100% {
    background-position: 0;
  }
}
@-webkit-keyframes shine {
  0% {
    background-position: 225px;
  }
  60% {
    background-position: 0;
  }
  100% {
    background-position: 0;
  }
}
@-o-keyframes shine {
  0% {
    background-position: 255px;
  }
  60% {
    background-position: 0;
  }
  100% {
    background-position: 0;
  }
}
@keyframes shine {
  0% {
    background-position: 255px;
  }
  60% {
    background-position: 0;
  }
  100% {
    background-position: 0;
  }
}


@media only screen and (max-width: 768px) {
  @keyframes logo-runLoading {
    0% {
      //   width: 0;
      transform: scale(0);
    }
    50% {
      // width: 30%;
      transform: scale(70%);
      padding: 20px;
      opacity: 1;
      margin-top: 0px;
    }
    90% {
      // width: 30%;
      transform: scale(70%);
      padding: 20px;
      opacity: 1;
      margin-top: 0px;
    }
    100% {
      // width: 30%;
      transform: scale(70%);
      padding: 20px;
      opacity: 1;
      margin-top: 0px;
      margin-top: -32.5vh;
    }
  }
}