.box-icon-topbar {
    margin-left: 20px;
    width: 80%;
    display: flex;
    justify-content: flex-end;
    i {
      color: $black;
      font-size: 1.2em;
    }
  }

.topbar {
    background-color: $gold;
    width: 100%;
    border-radius: 0px 0px 20px 20px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    z-index: 1;
    p {
        margin-right: 20px;
        font-weight: bold;
        width: 100%;
    }
}