.setting-option-button-device {
    background-color: $black_low;
    color: $gold;
    padding: 10px;
    border-radius: 10px;
    border: 0.5px solid $gold;
    box-shadow: 0px 0px 10px -2px $gold inset;
    -webkit-box-shadow: 0px 0px 10px -2px $gold inset;
    -moz-box-shadow: 0px 0px 10px -2px $gold inset;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    i {
        font-size: 2em;
    }
}

.box-option-button-device {
    display: flex;
    align-items: center;
}


.box-option-button-device-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 7.5px;
 p {
    margin: 0;
 }
}

.device-android {
    mask: url("../../../asset/image/android.svg");
    display: inline-block;
    width: 50px;
    height: 50px;
    mask-size: cover;
    background: $gold;
}

.device-ios {
    mask: url("../../../asset/image/ios.svg");
    display: inline-block;
    width: 50px;
    height: 50px;
    mask-size: cover;
    background: $gold;
}

.device-web {
    mask: url("../../../asset/image/web.svg");
    display: inline-block;
    width: 50px;
    height: 50px;
    mask-size: cover;
    background: $gold;
}