.setting-change-password-box {
    display: flex;
    flex-direction: column;
    margin: 10px 0px;
    label {
        margin: 5px 0px;
    }
}

.box-btn-setting-change-password {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: 30px;
    button {
        width: inherit;
    }
    button:nth-child(1) {
        margin-left: 10px;
    }
    button:nth-child(2) {
        margin-right: 10px;
    }
}