
.modal-option {
  /* fixed position will always show modal in the top left corner */
  position: fixed;
  top: 0;
  left: 0;
  /* z-index to show modal even if it is used before some other elements */
  z-index: 5;
  /* make modal fill whole window */
  width: 100vw;
  height: 100vh;
  /* add some background to blur content behind modal */
  // background: rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0);
  backdrop-filter: blur(10px);
  /* center content inside modal */
  display: flex;
  justify-content: center;
  align-items: center;
}

.compoenent-modal-option {
  background-color: $black_low;
  border: 1px solid $gold;
  border-radius: 20px;
  margin-right: 20px;
  margin-left: 20px;
}

.modal-option-topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 20px;
  h1 {
    color: $gold;
    font-size: 1.1em;
  }
  i {
    font-size: 2em;
    color: $gold;
  }
}


.btn-modal-option-cancel {
  width: 101%;
  background-color: transparent;
  border: 1px solid $gold;
  border-radius: 5px;
  height: 25px;
  color: $gold;
  margin-top: 20px;
    margin-bottom: 20px;
}

.btn-modal-option-ok {
  width: 101%;
  background-color: $gold;
  border: 1px solid $gold;
  border-radius: 5px;
  height: 25px;
  color: $black;
  margin-top: 20px;
    margin-bottom: 20px;
}