.main-userAccounts {
    background-color: $black_low;
    height: 68px;
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
    border-radius:0px 0px 20px 20px;
}

.box-information-userAccounts {
    width: 100%;
    display: flex;
    align-items: center;
    // margin-right: 20px;
}

.icon-userAccounts {
    mask: url("../../asset/image/user.svg");
    display: inline-block;
    width: 45px;
    height: 45px;
    background: $gold;
    mask-size: cover;
    margin-right: 20px;
}

.text-box-information-userAccounts {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    h3 {
        margin: 0.1em;
    }
    h6 {
        margin: 0.1em;
    }

}

.box__icon {
    margin-left: 20px;
    display: flex;
    align-items: center;
    transform: rotate(0deg);
    i {
        color: $gold;
        font-size: 1.5em;
    }
}



.between-collapse-and-item {
    width: 100%;
}


.collapse 
{
    background-color: $black_low;
    height: 68px;
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
    border-radius:0px 0px 20px 20px;
    z-index: 100;
    &__header
    {
        padding-left:10px;
        padding-right:10px;
        // height:50px;
        background-color: $black_low;
        display:flex;
        align-items:center;
        justify-content:space-between;
        margin-bottom:2px;
    }

    &__svg 
    {
        width:15px;
        height:15px;
    }

    &__content 
    {
        padding:10px;
        height:0px;
        transition-duration:0.2s;
        transition-property: all;
        opacity:0;
    }

    &__icon
    {
        // padding:10px;
        // height:0px;


    }

    &__checkbox
    {
        display:none;
    }

    &__checkbox:checked + label + &__content 
    {
        height:fit-content;
        opacity:1;
       
    }   

    
}

.collapse__content {
    background-color: $black_low;
    border-radius:0px 0px 20px 20px;
    margin-top: -2px;
}