.input-setting {
    background: transparent;
    border: 1px solid $gold;
    border-radius: 8px;
    padding: 5px;
}

.setting-btn-ok {
    background-color: $gold;
    border: 1px solid $gold;
    color: $black;
    padding: 5px;
    font-weight: bold;
    border-radius: 8px;
}

.setting-btn-cancel {
    background-color: transparent;
    border: 1px solid $gold;
    color: $gold;
    padding: 5px;
    font-weight: bold;
    border-radius: 8px;
}

.container-setting-add-box {
    display: flex;
    justify-content: flex-end;
}
.setting-add-box {
    position: fixed;
    bottom: 10vh;
    margin-left: 10px;
    background-color: $black_low;
    color: $gold;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 0.5px solid $gold;
    box-shadow: 0px 0px 10px -2px $gold inset;
    -webkit-box-shadow: 0px 0px 10px -2px $gold inset;
    -moz-box-shadow: 0px 0px 10px -2px $gold inset;
    i {
        font-size: 2em;
    }
}