.box-search-select {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.box-input-search {
    width: 80%;
    display: flex;
    justify-content: center;
}

.input-search {
    width: 100%;
    background-color: $black !important;
    color: #B28228;
    padding: 10px;
    border-radius: 10px;
    border: 0.5px solid #B28228;
}

.box-search-select {
    width: 100%;
}
.ul-select-search {
    width: 80%;
    padding: 0;
    max-height: 70vh;
    overflow: auto;
    li {
        margin: 10px 0px 10px 0px;
        padding: 5px;
        // border: 1px solid $gold;
        // background-color: $black_low;
        // color: $gold;
        border-radius: 10px;
        color: $gold;
    }
}

.compoenent-modal-search {
    background-color: $black_low;
    border-radius: 20px 20px 0px 0px;
  }
  
  .modal-search-topbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 20px;
    h1 {
      color: $gold;
      font-size: 1.1em;
    }
    i {
        color: $gold;
      font-size: 2em;
    }
  }