.main-remote {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.box-middel-remote {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.box-car-remote {
  width: 20vh;
}

.car-remote {
  width: 100%;
}

.btn-little-remote-left {
  background-color: $black_low;
  color: $gold;
  padding: 2px 20px 2px 20px;
  border-radius: 10px 0px 0px 10px;
  border: 0.5px solid $gold;
  box-shadow: 0px 0px 10px -2px $gold inset;
  -webkit-box-shadow: 0px 0px 10px -2px $gold inset;
  -moz-box-shadow: 0px 0px 10px -2px $gold inset;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  span {
    mask: url("../../../asset/image/list.svg");
    display: inline-block;
    width: 30px;
    height: 30px;
    background: $gold;
    mask-size: cover;
  }
}

.btn-little-remote-right {
  background-color: $black_low;
  color: $gold;
  padding: 5px 15px 2px 15px;
  border-radius: 0px 10px 10px 0px;
  border: 0.5px solid $gold;
  box-shadow: 0px 0px 10px -2px $gold inset;
  -webkit-box-shadow: 0px 0px 10px -2px $gold inset;
  -moz-box-shadow: 0px 0px 10px -2px $gold inset;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  // span {
  //     mask: url("../../asset/image/user.svg");
  //     display: inline-block;
  //     width: 45px;
  //     height: 45px;
  //     background: $gold;
  //     mask-size: cover;
  // }
  i {
    color: $gold;
    font-size: 1.8em;
  }
}

.box-options-remote {
  display: flex;
  width: 100%;
  margin-top: 50px;
  flex-direction: column;
}

.box-option-remote {
  display: flex;
  width: 100%;
}

.option-remote {
