/////////////////////////////////// modal start



.ReactModal__Overlay {
  background-color: rgba(255, 255, 255, 0)  !important;
}

.ReactModalPortal:nth-child(1) {
background-color: rgba(255, 255, 255, 0) !important
}

.modal-remote {
  /* fixed position will always show modal in the top left corner */
  position: fixed;
  top: 0;
  left: 0;
  /* z-index to show modal even if it is used before some other elements */
  z-index: 5;
  /* make modal fill whole window */
  width: 100vw;
  height: 100vh;
  /* add some background to blur content behind modal */
  // background: rgba(255, 255, 255, 0.8);
  background: rgba(255,255,255,0);
backdrop-filter: blur(10px);
  /* center content inside modal */
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.compoenent-modal-remote {
  background-color: $gold;
  border-radius: 20px 20px 0px 0px;
}

.modal-remote-topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 20px;
  h1 {
    color: $black;
    font-size: 1.1em;
  }
  i {
    font-size: 2em;
  }
}


.modal-box-options-remote {
