.loading-main {
    z-index: 1001;
position: fixed;
width: 100vw;
height: 100vh;
background: rgba(255, 255, 255, 0.07);

box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(14.7px);
-webkit-backdrop-filter: blur(14.7px);
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
}

.img-loading {
    width: 30%;
}

.text-loading {
color: $gold;
font-weight: bold;
}

