#range {
    width: 100%;
}

#range {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    background: $black;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
    border: 1px solid $gold;
    border-radius: 20px;
  }
  
  #range:hover {
    opacity: 1;
  }
  
  #range::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    background: $gold;
    border-radius: 50%;
    cursor: pointer;
  }
  
  #range::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: $gold;
    border-radius: 50%;
    cursor: pointer;
  }

.range-number {
    display: flex;
    justify-content: space-between;
    color: $gold;
}